import axios from 'axios';
import { OrderData } from './orders'; // or just use directly if same file

const API_URL = 'https://beige-dinosaur-443055.hostingersite.com/wp-json/wc/v3';
const CONSUMER_KEY = 'ck_3392276ae29bf23be711650b41811e45cf26657d';
const CONSUMER_SECRET = 'cs_3b19af7ccb87b720323c3993a382ce29666d63e4';


export async function createOrder(orderData: OrderData) {
  const authParams = {
    consumer_key: CONSUMER_KEY,
    consumer_secret: CONSUMER_SECRET,
  };

  const response = await axios.post(API_URL, orderData, {
    params: authParams,
    headers: {
      'Content-Type': 'application/json',
    },
  });

  return response.data;
}